
  import { defineComponent, ref } from 'vue';
  export default defineComponent({

    setup() {
        const value = ref<string>('');
        const query=()=>{
          // value.value=''
          window.location.href="/MoliVideo/#/movue?wd=" + value.value
      


        }
      return {
        selectedKeys: ref<string[]>([]),
            value,
            query
      };
    },
  });
  